import React from "react";
import styled from "styled-components";

function Figures() {
  return (
    <FiguresContainer>
      <div>
        <span>+ 140</span>
        <span>EXPERT COACHES</span>
      </div>
      <div>
        <span>+ 978</span>
        <span>MEMBERS JOINED</span>
      </div>
      <div>
        <span>+ 50</span>
        <span>FITNESS PROGRAMS</span>
      </div>
    </FiguresContainer>
  );
}

export default Figures;

const FiguresContainer = styled.div`
  display: flex;
  gap: 2rem;
  color: white;

  > div {
    display: flex;
    flex-direction: column;
  }
  > div > span:first-child {
    font-size: 2rem;
  }
  > div > span:last-child {
    color: var(--gray);
    font-size: 0.7rem;
    font-weight: bold;
  }
`;
