import React from "react";
import styled from "styled-components";

function MainNavigation() {
  return (
    <Navigation>
      <ul>
        <li>Home</li>
        <li>Programs</li>
        <li>Why Us</li>
        <li>Plans</li>
        <li>Testimonials</li>
      </ul>
    </Navigation>
  );
}

export default MainNavigation;

const Navigation = styled.nav`
  > ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    gap: 2rem;
  }
  > ul li {
    cursor: pointer;
    min-width: fit-content;
  }
  > ul li:hover {
    color: var(--orange);
  }
`;
